<template>
    <div>
    <!--头部-->
    <header id="head_top">
        <section class="head_goback" @click="goBack">
            <i class="el-icon-arrow-left"></i>
        </section>
        <section class="title_head ellipsis">
                <span class="title_text">
                    订单列表
                </span>
        </section>
    </header>
     <ul class="order_list_ul">
        <li class="order_list_li" v-for="(v,k) in order" :key="v._id">
            <img :src="'https://elm.cangdu.org/img/'+v.restaurant_image_url"  alt="" class="restaurant_image">
            <section class="order_item_right">
                <section>
                    <header class="order_item_right_header">
                        <section class="order_header">
                            <h4 @click="goOrderDetail(v,k)">
                                <span>{{v.restaurant_name}}</span>
                                <i class="el-icon-arrow-right"></i>
                            </h4>
                            <p class="order_time">{{v.formatted_created_at}}</p>
                        </section>
                        <p class="order_status">支付超时</p>
                    </header>
                    <section class="order_basket">
                        <p class="order_name">{{v.basket?v.basket.group[0][0].name:''}}等{{v.basket.group[0].length}}件商品</p>
                        <p class="order_amount">￥{{v.basket.packing_fee.price}}.00</p>
                    </section>

                </section>
                <div class="order_again">
                    <div class="page" @click="goShop(k)">
                        <span class="buy_again">再来一单</span>
                    </div>
                </div>
            </section>
        </li>
     </ul>
    <section id="foot_guide">
        <section class="guide_item" @click="take_out_food">
            <i class="el-icon-eleme"></i>
            <span>外卖</span>
        </section>
        <section class="guide_item" @click="goSearch">
            <i class="el-icon-search"></i>
            <span>搜索</span>
        </section>
        <section class="guide_item" >
            <i class="el-icon-tickets"></i>
            <span>订单</span>
        </section>
        <section class="guide_item" @click="goMy">
            <i class="el-icon-user"></i>
            <span>我的</span>
        </section>
    </section>
    </div>
</template>

<script>
    export default {
        name: "Order",
        data(){
            return{
                user:[],
                order:[],
                i:0,
                latitude:"",
                longitude:""

            }
        },
        created(){
            if(localStorage.getItem("user")){
                this.user = JSON.parse(localStorage.getItem("user"))
            }
            this.placeHistory = JSON.parse(localStorage.getItem('placeHistory'))
            // console.log(this.placeHistory);
            this.end = this.placeHistory?this.placeHistory.slice(-1):null
            // console.log(this.end)
            //如果是未选地区
            if (this.placeHistory == null) {
                this.address ={
                    name: "上海迪士尼乐园",
                    address: "上海市浦东新区申迪西路753号",
                    latitude: 31.14419,
                    longitude: 121.66034,
                    geohash: "31.14419,121.66034"
                }
                this.latitude = this.address.latitude;
                this.longitude = this.address.longitude;
                this.geohash = this.address.geohash;

            }else {
                //选择了地点根据地点获取轮播图店铺信息
                this.geohash = this.end[0].geohash
                this.latitude = this.end[0].latitude;
                this.longitude = this.end[0].longitude;
            }
            this.axios.get("bos/v2/users/"+this.user[0].user_id+"/orders?limit=10&offset=0").then(res=>{
                this.order = res;
                console.log(res);
                console.log(this.order[0].basket.group[0][0].name);
            }).catch(err=>{

            })
        },
        methods:{
            //头部返回上一级
            goBack(){
                this.$router.back(-1);
            },
            take_out_food(){
                this.$router.push({
                    path:'/msite'
                })
            },
            goSearch(){
                this.$router.push({
                    path:'/search'
                })
            },
            goMy(){
                this.$router.push({
                    path:'/profile'
                })
            },
            goShop(k){
                this.$router.push({
                    path:"/shop",
                    query:{
                        shopid:this.order[k].restaurant_id,
                        latitude:this.latitude,
                        longitude:this.longitude,
                        // geohash:this.$route.query.geohash,
                    }

                })
            },
            goOrderDetail(v){
                this.$store.commit("orderDe",v);
                this.$router.push({
                    path:"/orderDetail",
                })
            }
        }
    }
</script>

<style scoped>
    #head_top{
        background-color: #3190e8;
        position: fixed;
        z-index: 100;
        left: 0;
        top: 0;
        width: 100%;
        height: 1.95rem;
    }
    .head_goback{
        left: .4rem;
        width: .6rem;
        height: 1rem;
        line-height: 2.2rem;
        margin-left: .4rem;
        color: #fff;
    }
    .title_head{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        width: 50%;
        color: #fff;
        text-align: center;
    }
    .title_text {
        font-size: .8rem;
        color: #fff;
        text-align: center;
        font-weight: 700;
    }
    #foot_guide {
        background-color: #fff;
        position: fixed;
        z-index: 100;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 2rem;
        display: flex;
        box-shadow: 0 -0.02667rem 0.05333rem rgba(0,0,0,.1);
    }
    .guide_item {
        flex: 1;
        display: flex;
        text-align: center;
        flex-direction: column;
        align-items: center;
    }
    .guide_item span{
        font-size: .45rem;
        color: #666;
        margin-top: .2rem;
        text-align: center;
        margin-left: .2rem;
    }
    .guide_item i {
        width: .8rem;
        height: .8rem;
        margin-top: .3rem;
        fill: #ccc;
    }
    .guide_item:nth-of-type(3) i {
        color: #3190e8;
    }
    .order_list_ul {
        padding-top: 1.95rem;
    }
    .order_list_li {
        background-color: #fff;
        display: flex;
        margin-bottom: .5rem;
        padding: .6rem .6rem 0;
    }
    .order_list_li .restaurant_image {
        width: 2rem;
        height: 2rem;
        margin-right: .4rem;
    }
    .order_item_right {
        flex: 5;
    }
    .order_item_right_header{
        border-bottom: .025rem solid #f5f5f5;
        padding-bottom: .3rem;
        display: flex;
        justify-content: space-between;
    }
    .order_status {
        font-size: .6rem;
        color: #333;
    }
    .order_header h4{
        display: flex;
        align-items: center;
        justify-content: flex-start;
        font-size: .75rem;
        color: #333;
        line-height: 1rem;
        width: 9rem;
    }
    .order_header h4 i {
       font-size: .4rem;
        color: #ccc;
        margin-right: .2rem;
    }
    .order_time{
        font-size: .55rem;
        color: #999;
        line-height: .8rem;
    }
    .order_basket {
        display: flex;
        justify-content: space-between;
        line-height: 2rem;
        border-bottom: .025rem solid #f5f5f5;
    }
    .order_basket .order_name{
        font-size: .6rem;
        color: #666;
        width: 10rem;
    }
    .order_amount{
        font-size: .6rem;
        color: #f60;
        font-weight: 700;
    }
    .order_again{
        text-align: right;
        line-height: 1.6rem;
    }
    .page {
        display: inline-block;
    }
    .buy_again {
        font-size: .55rem;
        color: #3190e8;
        border: .025rem solid #3190e8;
        padding: .1rem .2rem;
        border-radius: .15rem;
    }
</style>